.site-blocks-table {
    overflow: auto; }
    .site-blocks-table .product-thumbnail {
      width: 200px; }
    .site-blocks-table .btn {
      padding: 2px 10px; }

    .site-blocks-table thead th {
      padding: 30px;
      text-align: center;
      border-width: 0px !important;
      vertical-align: middle;
      color: rgba(0, 0, 0, 0.8);
      font-size: 18px; 
    }
    .site-blocks-table td {
      padding: 20px;
      text-align: center;
      vertical-align: middle;
      color: rgba(0, 0, 0, 0.8); 
    }
    
    .site-blocks-table tbody tr:first-child td {
      border-top: 1px solid #3b5d50 !important; }
 
  
  .site-block-order-table th {
    border-top: none !important;
    border-bottom-width: 1px !important; }
  
  .site-block-order-table td, .site-block-order-table th {
    color: #000000; }


/* ======================== */
.untree_co-section {
    padding: 7rem 0; 
}

/* ======================= */

.before-footer-section {
padding: 2rem 0 8rem 0 !important; 
}


.cart-section {
  height: 500px; /* Fixed height for desktop */
  background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url('https://res.cloudinary.com/elonatech/image/upload/v1710840087/productHeaderPage/shopping_cart_page_q2r8rq.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding: 20px;
  /* margin-bottom: 50px; */
}

/* Text styling for desktop */
.cart-section h2 {
  font-size: 2rem;
  line-height: 1.2;
  margin-bottom: 10px;
}

/* Mobile view */
@media (max-width: 576px) {
  .cart-section {
  height: 200px; 
  background-image: linear-gradient(
  0deg,
  rgba(0, 0, 0, 0.5),
  rgba(0, 0, 0, 0.5)
),
url('https://res.cloudinary.com/elonatech/image/upload/v1730211777/shopping_cart_qctior.png');
  background-size: cover;
  /* flex-direction: column; */
  align-items: center;
  padding-top: 50px;
  }

  .cart-section h2 {
    margin: 0;
    padding: 0;
  }

.cart-btn {
    font-weight: 600;
    padding: 12px 30px;
    border-radius: 30px;
    color: #ffffff;
    background: #2f2f2f;
    border-color: #2f2f2f; 
 }

 .red-cart-btn{
  font-weight: 600;
  padding: 12px 30px;
  border-radius: 30px;
  color: #ffffff;
  background: #dc3545;
  border-color: #dc3545; 
 }

}

  
 .rmFocus:focus {
    border-color: #ced4da; /* default B5 color or set your own color*/
    border: none !important; /* if you want to remove borders at all*/
    outline: none !important;
    box-shadow: none !important;
 }



 .thankyou-icon {
  position: relative;
  color: #3b5d50; }
  .thankyou-icon:before {
    position: absolute;
    content: "";
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: rgba(59, 93, 80, 0.2); }
    


